import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getStudents = dataSearch =>
  request({
    prefix,
    url: `/Student/Search`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const getPaperStudent = studentId =>
  request({
    prefix,
    url: `/Paper_Student/BrowsePapersByStudent`,
    params: { studentId },
    method: 'GET',
  });

export const downloadFileEstimatedNote = dataUpdate =>
  requestDownload({
    defaultFileName: 'fileDownload.xlsx',
    prefix,
    url: `/Student/ExportStudentWithEstimatedNote`,
    data: dataUpdate,
    method: 'POST',
  });



