import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave } from 'components/common/button';
import { Card, Row, Col, Tag } from 'antd';
import { TableData } from 'components/common';
import { getStudents, getPaperStudent } from 'services/AdditionalAdmissionPapersPage';

import FormSearchStudent from './FormSearchStudent';

class ReceiveAdmissionPapersPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: [],
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { reload, paramSearchClass } = this.state;
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        if (selectedRows.length > 0) {
          getPaperStudent(selectedRows[0].studentId)
            .then(res => {
              console.log('res', res);
            })
            .catch(error => {
              console.log('error', error);
            });
        }
      },
      getCheckboxProps: record => ({
        disabled: record.status === 'Đủ', // Column configuration not to be checked
        name: record.name,
      }),
    };
    const paperColumns = [
      {
        title: 'Trạng thái',
        dataIndex: 'status',
        key: 'code',
        render: status => (
          <Tag color={status === 'Thiếu' ? 'volcano' : 'green'} key={status}>
            {status}
          </Tag>
        ),
      },
      {
        title: 'Bắt buộc',
        dataIndex: 'isRequire',
        key: 'code',
      },
      {
        title: 'Tên giấy tờ',
        dataIndex: 'name',
        key: 'code',
      },
      {
        title: 'Số lượng',
        dataIndex: 'number',
        key: 'code',
      },
    ];
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'toScores',
      },
    ];
    return (
      <Fragment>
        <Card title="Nhận giấy tờ bổ sung">
          <Row>
            <Col span={24}>
              <FormSearchStudent dataForm={this.dataFormSeach} />
            </Col>

            <Col span={24}>
              <TableData
                rowSelection={rowSelection}
                reload={reload}
                formSearch
                service={getStudents}
                paramSearch={paramSearchClass}
                columns={columns}
                rowKey="id"
              />
              {/* <Table
                rowSelection={rowSelection}
                bordered
                dataSource={commentEndCourse}
                columns={columns}
              /> */}
            </Col>
          </Row>
        </Card>
        <Card title="Thông tin giấy tờ">
          <Row>
            <Col span={24}>
              <Row>
                <TableData
                  rowSelection={rowSelection}
                  // reload={reload}
                  formSearch
                  service={getStudents}
                  paramSearch={paramSearchClass}
                  columns={paperColumns}
                  rowKey="code"
                />
              </Row>
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



